import React from "react";
import { color, util } from "../../../../public/styles";
import BlcButtonBase from "../../BlcButtonBase";
import PropTypes from "prop-types";
/**
 * Success button style.
 */
const blcButtonSuccess = props => (
	<BlcButtonBase
		disabled={props.disabled}
		size={util.blcButtonSize(props.size)}
		margin={props.margin}
		padding={util.blcButtonPadding(props.size)}
		rounded={props.rounded ? util.blcButtonRadius(props.size) : 0}
		color={props.inverted ? color.success : color.white}
		colorHover={props.inverted ? color.successLight : color.white}
		colorFocus={props.inverted ? color.successDark : color.white}
		colorDisabled={props.inverted ? color.gray : color.grayDark}
		border={color.success}
		borderHover={props.inverted ? color.successLight : color.success}
		borderFocus={props.inverted ? color.successDark : color.success}
		borderDisabled={props.inverted ? color.gray : color.grayLight}
		background={props.inverted ? color.transparent : color.success}
		backgroundHover={props.inverted ? color.transparent : color.successLight}
		backgroundFocus={props.inverted ? color.transparent : color.successDark}
		backgroundDisabled={props.inverted ? color.transparent : color.grayLight}
	>
		{props.children}
	</BlcButtonBase>
);

blcButtonSuccess.propTypes = {
	children: PropTypes.node,
	disabled: PropTypes.bool,
	inverted: PropTypes.bool,
	margin: PropTypes.string,
	rounded: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
	size: PropTypes.oneOf(["", "largest", "larger", "smaller", "smallest"])
};

blcButtonSuccess.defaultProps = {
	disabled: false,
	inverted: false,
	margin: "",
	rounded: "",
	size: ""
};

export default blcButtonSuccess;
