import React from 'react';
import t from 'prop-types';
import { size, weight, color, util } from "../../../public/styles";

const blcCode = props => (
	<pre>
		{props.children}

		<style jsx>{`
			background-color: ${props.background};
			color: ${props.color};
			display: inline-block;
			font-family: monospace;
			font-size: ${util.blcParagraphSize(props.size)};
			font-weight: ${weight.regular};
			line-height: ${util.blcParagraphLineHeight(props.size)};
			margin: 0;
			padding: 0 ${size.marginSmallest};
			text-align: ${props.textAlign};
		`}</style>
	</pre>
);

blcCode.propTypes = {
	background: t.string,
	children: t.node,
	color: t.string,
	size: t.oneOf(['largest', 'larger', 'smaller', 'smallest', 'inherit']),
	textAlign: t.oneOf(['left', 'center', 'right'])
}

blcCode.defaultProps = {
	background: color.smoke,
	children: null,
	color: color.alert,
	size: 'inherit',
	textAlign: 'left'
}

export default blcCode;
