import React from 'react';
import t from 'prop-types';
import { color, util, weight } from '../../../public/styles';

const blcLabel = props => (
	<label>
		{props.children}

		<style jsx>{`
			color: ${props.inverted ? (props.color ? props.color : color.smokeLight) : (props.color ? props.color : color.grayDark)};
			display: inline-block;
			font-family: MarkPro;
			font-size: ${util.blcParagraphSize(props.size)};
			font-stretch: normal;
			font-style: normal;
			font-weight: ${weight.bold};
			letter-spacing: normal;
			line-height: ${util.blcParagraphLineHeight(props.size)};
			padding: ${props.padding} 0;
			text-align: ${props.textAlign};
			width: 100%;
		`}</style>
	</label>
);

blcLabel.propTypes = {
	children: t.node,
	color: t.string,
	inverted: t.bool,
	padding: t.string,
	size: t.oneOf(['largest', 'larger', 'smaller', 'smallest', 'inherit']),
	textAlign: t.oneOf(['left', 'center', 'right'])
}

blcLabel.defaultProps = {
	children: null,
	inverted: false,
	padding: '0',
	size: 'smaller',
	textAlign: 'left'
}

export default blcLabel;
