import React from 'react';
import t from 'prop-types';
import Link from 'next/link'
import { weight, color, util } from "../../../public/styles";

const blcLink = props => (
	<Link href={props.href}>
		<a target={props.target}>
			{props.children}

			<style jsx>{`
				color: ${props.color};
				display: inline-block;
				font-size: ${util.blcParagraphSize(props.size)};
				font-weight: ${weight.regular};
				line-height: ${util.blcParagraphLineHeight(props.size)};
				text-align: ${props.textAlign};
				text-decoration: underline;
			`}</style>
		</a>
	</Link>
);

blcLink.propTypes = {
	children: t.node,
	color: t.string,
	href: t.string,
	size: t.oneOf(['largest', 'larger', 'smaller', 'smallest', 'inherit']),
	target: t.oneOf(['_blank', '_self', '_parent', '_top']),
	textAlign: t.oneOf(['left', 'center', 'right'])
}

blcLink.defaultProps = {
	children: null,
	color: color.grayDark,
	href: '#',
	size: 'inherit',
	target: '_self',
	textAlign: 'left'
}

export default blcLink;
