import React from "react";
import PropTypes from "prop-types";
import { color, util } from "../../../../public/styles";
import BlcButtonBase from "../../BlcButtonBase";

const blcButtonTransport = props => (
	<BlcButtonBase
		disabled={props.disabled}
		size={util.blcButtonSize(props.size)}
		margin={props.margin}
		padding={util.blcButtonPadding(props.size)}
		rounded={util.blcButtonRadius(props.size)}
		color={color.white}
		colorHover={color.white}
		colorFocus={color.white}
		colorDisabled={color.grayDark}
		background={props.inverted ? color.asphalt : color.transport}
		backgroundHover={props.inverted ? color.asphaltLight : color.transportLight}
		backgroundFocus={props.inverted ? color.asphaltDark : color.transportDark}
		backgroundDisabled={color.grayLight}
	>
		{props.children}
	</BlcButtonBase>
);

blcButtonTransport.propTypes = {
	children: PropTypes.node,
	disabled: PropTypes.bool,
	inverted: PropTypes.bool,
	margin: PropTypes.string,
	size: PropTypes.oneOf(["", "largest", "larger", "smaller", "smallest"])
};

blcButtonTransport.defaultProps = {
	disabled: false,
	inverted: false,
	margin: "",
	size: ""
};

export default blcButtonTransport;
