import React from "react";
import t from "prop-types";
import BlcTitleBase from "../BlcTitleBase";
import { size } from "../../../public/styles";

const blcH1 = props => (
	<BlcTitleBase
		color={props.color}
		inverted={props.inverted}
		margin={props.margin}
		nowrap={props.nowrap}
		textAlign={props.textAlign}
	>
		<h1 id={props.id}>
			{props.children}

			<style jsx>{`
				color: inherit;
				font-size: ${size.h1};
				font-weight: inherit;
				line-height: ${size.h1LineHeight};
				margin: inherit;
				white-space: inherit;
			`}</style>
		</h1>
	</BlcTitleBase>
);

blcH1.propTypes = {
	color: t.string,
	inverted: t.bool,
	margin: t.string,
	nowrap: t.bool,
	textAlign: t.oneOf(['left', 'center', 'right'])
}

blcH1.defaultProps = {
	inverted: false,
	margin: '0',
	nowrap: false,
	textAlign: 'left'
}

export default blcH1;
