import React from 'react';
import t from 'prop-types';
import { color, size } from '../../../public/styles';

const blcBox = props => (
	<div>
		{props.children}

		<style jsx>{`
			background-color: ${props.background};
			border-color: ${props.borderColor};
			border-style: ${props.borderStyle};
			border-width: ${props.borderWidth}px;
			margin: 0 auto;
			overflow: ${props.overflow};
			padding: ${props.padding};
		`}</style>
	</div>
);

blcBox.propTypes = {
	background: t.string,
	borderColor: t.string,
	borderStyle: t.oneOf(['solid', 'dotted', 'dashed']),
	borderWidth: t.number,
	overflow: t.oneOf(['visible', 'hidden', 'scroll', 'auto']),
	padding: t.string
}

blcBox.defaultProps = {
	background: color.smokeLight,
	borderColor: color.smokeDark,
	borderStyle: 'solid',
	borderWidth: 0,
	overflow: 'visible',
	padding: size.marginSmaller
}

export default blcBox;
