import React from "react";
import Link from "next/link";
import t from "prop-types";

const BlcLogoBase = props => (
	<h1>
		<Link href={props.href}>
			<a>{props.children}</a>
		</Link>

		<style jsx>{`
			display: inline-block;
			font-size: 0;
			height: fit-content;
			margin: ${props.margin}px;
			width: fit-content;

			a {
				display: block;
				height: fit-content;
				width: fit-content;
			}
		`}</style>
	</h1>
);

BlcLogoBase.propTypes = {
	href: t.string,
	margin: t.number
}

BlcLogoBase.defaultProps = {
	href: '/',
	margin: 0
}

export default BlcLogoBase;
