import React from 'react';
import t from 'prop-types';
import { size, util } from '../../../public/styles';

const blcColumn = props => (
	<div>
		{props.children}

		<style jsx>{`
			align-self: ${props.align};
			order: ${props.order};
			width: calc(
				${100 / size.columns * props.col}%
				- ${util.blcColumnGutter(props.gutter)}
			);
		`}</style>
	</div>
);

blcColumn.propTypes = {
	align: t.oneOf(['auto', 'stretch', 'flex-start', 'flex-end', 'center']),
	col: t.number,
	gutter: t.oneOf(['smallest', 'smaller', 'inherit', 'larger', 'largest']),
	order: t.number
}

blcColumn.defaultProps = {
	align: 'auto',
	col: 12,
	order: 0
}

export default blcColumn;
