import React from "react";
import t from "prop-types";
import { weight, color } from "../../public/styles";

const blcTitleBase = props => (
	<div>
		{props.children}

		<style jsx>{`
			color: ${props.inverted ? (props.color ? props.color : color.white) : (props.color ? props.color : color.asphalt)};
			font-family: MarkPro;
			font-stretch: normal;
			font-style: normal;
			font-weight: ${weight.bold};
			letter-spacing: normal;
			margin: ${props.margin} 0;
			text-align: ${props.textAlign};
			white-space: ${props.nowrap ? 'nowrap' : 'normal'};
			width: 100%;
		`}</style>
  	</div>
);

blcTitleBase.propTypes = {
	color: t.string,
	inverted: t.bool,
	margin: t.string,
	nowrap: t.bool,
	textAlign: t.oneOf(['left', 'center', 'right'])
}

blcTitleBase.defaultProps = {
	inverted: false,
	margin: '0',
	nowrap: false,
	textAlign: 'left'
}

export default blcTitleBase;
