import React from 'react';
import t from 'prop-types';

const blcRow = props => (
	<div>
		{props.children}

		<style jsx>{`
			align-content: ${props.alignContent};
			align-items: ${props.alignItems};
			display: flex;
			flex-direction: ${props.direction};
			flex-flow: ${props.wrap};
			justify-content: ${props.justifyContent};
			margin: ${props.margin} auto;
			width: 100%;
		`}</style>
	</div>
);


blcRow.propTypes = {
	alignContent: t.oneOf(['flex-start', 'flex-end', 'center', 'space-between', 'space-around', 'space-evenly', 'stretch']),
	alignItems: t.oneOf(['stretch', 'flex-start', 'flex-end', 'center']),
	direction: t.oneOf(['row', 'row-reverse']),
	justifyContent: t.oneOf(['flex-start', 'flex-end', 'center', 'space-between', 'space-around', 'space-evenly']),
	margin: t.string,
	wrap: t.oneOf(['nowrap', 'wrap', 'wrap-reverse'])
}

blcRow.defaultProps = {
	alignContent: 'stretch',
	alignItems: 'stretch',
	direction: 'row',
	justifyContent: 'space-between',
	margin: '0',
	wrap: 'nowrap'
}


export default blcRow;
