import React from "react";
import t from "prop-types";
import { size, weight, color, unit } from "../../public/styles";

/*
 * The base component for all BLC buttons. Do NOT use this button,
 * instead look * at the buton components built upon this one.
 */

const blcButtonBase = props => (
	<button disabled={props.disabled ? true : false}>
		{props.children}

		<style jsx>{`
			appearance: none;
			background-color: ${props.background ? props.background : color.backlot};
			border: ${size.border} solid ${props.border ? props.border : (props.background ? props.background : color.backlot)};
			border-radius: ${props.rounded ? props.rounded : 0};
			color: ${props.color ? props.color : color.asphalt};
			cursor: pointer;
			font-family: MarkPro;
			font-size: ${props.size ? props.size : size.p};
			font-stretch: normal;
			font-style: normal;
			font-weight: ${weight.bold};
			letter-spacing: normal;
			line-height: 1;
			margin: ${props.margin ? props.margin : size.marginSmallest};
			padding: ${props.padding ? props.padding : unit(1.75) + ' ' + size.margin};
			text-align: center;
			transition: background .15s linear, border .15s linear;
			width: fit-content;

			button:hover {
				background-color: ${props.backgroundHover ? props.backgroundHover : color.backlotLight};
				border-color: ${props.borderHover ? props.borderHover : (props.backgroundHover ? props.backgroundHover : color.backlotLight)};
				color: ${props.colorHover ? props.colorHover : color.asphalt};
			}

			button:active {
				background-color: ${props.backgroundFocus ? props.backgroundFocus : color.backlotDark};
				border-color: ${props.borderFocus ? props.borderFocus : (props.backgroundFocus ? props.backgroundFocus : color.backlotDark)};
				color: ${props.colorFocus ? props.colorFocus : color.asphalt};
			}

			button:focus {
				outline: 0;
			}

			button:disabled,
			button[disabled] {
				background-color: ${props.backgroundDisabled ? props.backgroundDisabled : color.grayLight};
				border-color: ${props.borderDisabled ? props.borderDisabled : (props.backgroundDisabled ? props.backgroundDisabled : color.grayLight)};
				color: ${props.colorDisabled ? props.colorDisabled : color.grayDark};
				cursor: not-allowed;
			}
		`}</style>
	</button>
)

blcButtonBase.t = {
	background: t.string,
	backgroundDisabled: t.string,
	backgroundFocus: t.string,
	backgroundHover: t.string,
	border: t.string,
	borderDisabled: t.string,
	borderFocus: t.string,
	borderHover: t.string,
	children: t.node,
	color: t.string,
	colorDisabled: t.string,
	colorFocus: t.string,
	colorHover: t.string,
	disabled: t.bool,
	margin: t.string,
	padding: t.string,
	rounded: t.oneOfType([t.string, t.number]),
	size: t.string
};

blcButtonBase.defaultProps = {
	background: "",
	backgroundDisabled: "",
	backgroundFocus: "",
	backgroundHover: "",
	border: "",
	borderDisabled: "",
	borderFocus: "",
	borderHover: "",
	color: "",
	colorDisabled: "",
	colorFocus: "",
	colorHover: "",
	padding: "",
	disabled: false,
	margin: "",
	rounded: "",
	size: ""
};

export default blcButtonBase;
