import PropTypes from "prop-types";
import React from "react";
import { color, util } from "../../../public/styles";
import BlcButtonBase from "../BlcButtonBase";

/**
 * Secondary button style.
 */

const blcButtonSecondary = props => (
	<BlcButtonBase
		disabled={props.disabled}
		size={util.blcButtonSize(props.size)}
		margin={props.margin}
		padding={util.blcButtonPadding(props.size)}
		rounded={props.rounded ? util.blcButtonRadius(props.size) : 0}
		color={props.inverted ? color.smokeDark : color.asphalt}
		colorHover={props.inverted ? color.white : color.asphaltLight}
		colorFocus={props.inverted ? color.grayLight : color.asphaltDark}
		colorDisabled={props.inverted ? color.grayDark : color.gray}
		border={props.inverted ? color.smokeDark : color.asphalt}
		borderHover={props.inverted ? color.white : color.asphaltLight}
		borderFocus={props.inverted ? color.grayLight : color.asphaltDark}
		borderDisabled={props.inverted ? color.grayDark : color.gray}
		background={color.transparent}
		backgroundHover={color.transparent}
		backgroundFocus={color.transparent}
		backgroundDisabled={color.transparent}
	>
		{props.children}
	</BlcButtonBase>
);

blcButtonSecondary.propTypes = {
	children: PropTypes.node,
	disabled: PropTypes.bool,
	inverted: PropTypes.bool,
	margin: PropTypes.string,
	rounded: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
	size: PropTypes.oneOf(["", "largest", "larger", "smaller", "smallest"])
};

blcButtonSecondary.defaultProps = {
	disabled: false,
	inverted: false,
	margin: "",
	rounded: "",
	size: ""
};

export default blcButtonSecondary;
