import React from "react";
import PropTypes from "prop-types";
import { color, util } from "../../../../public/styles";
import BlcButtonBase from "../../BlcButtonBase";

/**
 * Float branded button.
 */

const blcButtonFloat = props => (
	<BlcButtonBase
		disabled={props.disabled}
		size={util.blcButtonSize(props.size)}
		margin={props.margin}
		padding={util.blcButtonPadding(props.size)}
		rounded={util.blcButtonRadius(props.size)}
		color={color.white}
		colorHover={color.white}
		colorFocus={color.white}
		colorDisabled={color.grayDark}
		background={props.inverted ? color.asphalt : color.float}
		backgroundHover={props.inverted ? color.asphaltLight : color.floatLight}
		backgroundFocus={props.inverted ? color.asphaltDark : color.floatDark}
		backgroundDisabled={color.grayLight}
	>
		{props.children}
	</BlcButtonBase>
);

blcButtonFloat.propTypes = {
	children: PropTypes.node,
	disabled: PropTypes.bool,
	inverted: PropTypes.bool,
	margin: PropTypes.string,
	size: PropTypes.oneOf(["", "largest", "larger", "smaller", "smallest"])
};

blcButtonFloat.defaultProps = {
	disabled: false,
	inverted: false,
	margin: "",
	size: ""
};

export default blcButtonFloat;
