import React from 'react';
import t from 'prop-types';
import { size } from '../../public/styles';

const blcGridBase = props => (
	<div>
		{props.children}

		<style jsx>{`
      margin: 0 auto;
      max-width: ${props.size};
      padding: 0 ${size.marginSmaller};
      width: 100%;
    `}</style>
	</div>
);

blcGridBase.propTypes = {
	size: t.string
}

blcGridBase.defaultProps = {
	size: '100%'
}

export default blcGridBase;
