import React from "react";
import { color, util } from "../../../../public/styles";
import BlcButtonBase from "../../BlcButtonBase";
import PropTypes from "prop-types";

/**
 * Alert button style.
 */
const blcButtonAlert = props => (
	<BlcButtonBase
		disabled={props.disabled}
		size={util.blcButtonSize(props.size)}
		margin={props.margin}
		padding={util.blcButtonPadding(props.size)}
		rounded={props.rounded ? util.blcButtonRadius(props.size) : 0}
		color={props.inverted ? color.alert : color.white}
		colorHover={props.inverted ? color.alertLight : color.white}
		colorFocus={props.inverted ? color.alertDark : color.white}
		colorDisabled={props.inverted ? color.gray : color.grayDark}
		border={color.alert}
		borderHover={props.inverted ? color.alertLight : color.alert}
		borderFocus={props.inverted ? color.alertDark : color.alert}
		borderDisabled={props.inverted ? color.gray : color.grayLight}
		background={props.inverted ? color.transparent : color.alert}
		backgroundHover={props.inverted ? color.transparent : color.alertLight}
		backgroundFocus={props.inverted ? color.transparent : color.alertDark}
		backgroundDisabled={props.inverted ? color.transparent : color.grayLight}
	>
		{props.children}
	</BlcButtonBase>
);

blcButtonAlert.propTypes = {
	children: PropTypes.node,
	disabled: PropTypes.bool,
	inverted: PropTypes.bool,
	margin: PropTypes.string,
	rounded: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
	size: PropTypes.oneOf(["", "largest", "larger", "smaller", "smallest"])
};

blcButtonAlert.defaultProps = {
	disabled: false,
	inverted: false,
	margin: "",
	rounded: "",
	size: ""
};

export default blcButtonAlert;
